body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar li {
    display: inline-block;
    text-align: center;
  }
  
  .itemLeft {
    width: 15%;
  }
  
  .navbar {
    background: #ffba5a;
    box-shadow: 0 10px 8px rgb(31, 25, 25), 0 10px 10px rgb(250, 4, 4);
  }
  
  li {
    color: rgb(0, 0, 0);
    padding: 15px 0;
    font-size: 32px;
    font-family: 'Russo One', sans-serif;

  }
  
.header{
	background-color: #665c84;
	position: relative;
	width: 100%;
}

h1{
	text-align: center;
	font-size: 110px;
	padding: 50px 0 20px 0; 
	font-family: 'Passion One', cursive;
	letter-spacing: 4px;
	text-shadow: 3px 2px #ffba5a;
	color: #ff7657;
}

h4{
	text-align: center;
	font-size: 28px;
	padding-bottom: 50px;
	color: antiquewhite;
	font-family: 'Pompiere', cursive;
	font-weight: 600;

}
.card {
    width: 190px;
    height: 220px;
    margin: 20px;
    box-shadow: 0 5px 10px rgb(31, 18, 18);
    border: 5px solid rgb(248, 60, 75);
    position: relative;
    border-radius: 50px;
  }

.footer{
	background-color: #490549;
	width: 100%;
	position: relative;
}

.footer p{
	color: white;
	text-align: center;
	padding: 10px 0;
	font-size: 15px;
	font-family: 'Baloo Bhaijaan', cursive;

}
.wrapper {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 25px 0 50px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  background: -webkit-linear-gradient(top, #f3c5bd 0%,#ea2803 0%,#ea2803 0%,#ea2803 1%,#ff6600 33%,#ff6600 33%,#ff6600 57%,#ff6600 63%,#ff6600 63%,#ff6600 76%,#ff6600 91%,#e86c57 100%,#c72200 100%);
  background: linear-gradient(to bottom, #f3c5bd 0%,#ea2803 0%,#ea2803 0%,#ea2803 1%,#ff6600 33%,#ff6600 33%,#ff6600 57%,#ff6600 63%,#ff6600 63%,#ff6600 76%,#ff6600 91%,#e86c57 100%,#c72200 100%);
}
